@charset "utf-8";

.kolonsport-site {
  .main {
    .scr-navi {
      max-width: 750px;
      border-bottom: 0;
    }
  }
  .prod-list-top {
    margin-bottom: 0;
    .scr-prod {
      margin-bottom: 0;
    }
  }
  .video-wrap {
    position: relative;
    width: 100%;
    height: 106.66vw;
    background-color: #000;
    iframe {
      width: 100% !important;
      height: 100% !important;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }
    .bg-poster {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-size: contain;
    }
    .video-play {
      position: absolute;
      width: 12.266vw;
      height: 12.266vw;
      top: 50%;
      left: 50%;
      background: url('/_ui/img/kolonsport/m/icn/icn-video-play.png') no-repeat;
      background-size: contain;
      transform: translate(-50%,-50%);
      img {
        width: 100%;
      }
    }
  }
  .belt-banner a {
    display: block;
    width: 100%;
    height: 26.66vw;
    img {
      width: 100%;
    }
  }

  // menu 
  .kolon-menu {
    .gnb {
      ul {
        li {
          width: 9.5vw;
          .cart {
            width: 8vw;
            height: 8vw;
            margin-top: -4.8vw;
            background: url('/_ui/img/kolonsport/m/icn/icn-cart.png') no-repeat center center;
            background-size: 4.266vw;
          }
        }
      }
    }
    nav {
      .main-menu {
        &.toggle-nemu {
          margin-right: 4.933vw;
          margin-bottom: 3.2vw;
          border-bottom: .266vw solid #eaeaea;
        }
        li {
          border: 0;
          &.home {
            > a {
              font-weight: 600;
            }
          }
          a {
            padding: 3.33vw 0 3.33vw 2vw;
          }
        }
      }
    }
  }
}

@media (min-width: 750px) {
  .kolonsport-site {
    .video-wrap {
      height: 800px;
      .video-play {
        width: 92px;
        height: 92px;
      }
    }
    .belt-banner {
      height: 200px;
    }

    //menu
    .kolon-menu {
      .gnb {
        ul {
          li {
            width: 71px;
            .cart {
              width: 60px;
              height: 60px;
              margin-top: -36px;
              background-size: 32px;
            }
          }
        }
      }
      nav {
        .main-menu {
          &.toggle-nemu {
            margin-right: 37px;
            margin-bottom: 24px;
            border-bottom: 2px solid #eaeaea;
          }
          li {
            a {
              padding: 25px 0 25px 15px;
            }
          }
        }
      }
    }
  }
}
